import C from '../constants/authConstants';

const initialState = {
	username: null,
	uid: null,
	status: C.AUTH_ANONYMOUS,
};

export default (state = initialState, action) => {
	switch (action.type) {
		case C.AUTH_OPEN:
			return {
				status: C.AUTH_AWAITING_RESPONSE,
				username: 'guest',
				uid: null
			};
		case C.AUTH_LOGIN:
			return {
				status: C.AUTH_LOGGED_IN,
				username: action.username,
				uid: action.uid
			};
		case C.AUTH_LOGOUT:
			return {
				status: C.AUTH_ANONYMOUS,
				username: 'guest',
				uid: null
			};
    case C.AUTH_ERROR:
      return {
        status: C.AUTH_ERROR,
        errorCode: action.errorCode,
        errorMessage: action.errorMessage,
        username: null,
        uid: null
      };

		default:
			return state
	}
}



